import React, { PropTypes, Component } from 'react';
import { FormattedRelative } from 'react-intl';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import Layout from '../../components/Layout';
import s from './Home.css';
import { Card, CardActions, CardHeader, CardMedia, CardTitle, CardText } from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';
import ActionStoreIcon from 'material-ui/svg-icons/action/store';
import ActionSearchIcon from 'material-ui/svg-icons/action/search';

import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';


const styles = {
    button: {
        margin: 12,
    },
    exampleImageInput: {
        cursor: 'pointer',
        position: 'absolute',
        top: 0,
        bottom: 0,
        right: 0,
        left: 0,
        width: '100%',
        opacity: 0,
    },
};



class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            IsOpenData: 'false'
        }
    }

    OpenAbout() {
        this.setState({
            IsOpenData: (this.state.IsOpenData == 'false') ? 'true' : 'false'
        })
        console.log(this.state);
    }


    render() {
        return (
            <div>
                <Layout name="polo">
                    <div className={s.root}>
                        <div className={s.container}>
                            {this.state.IsOpenData}
                            <Card>
                                <CardHeader
                                    title="URL Avatar"
                                    subtitle="Subtitle"
                                    avatar="images/s1.jpg"
                                    />
                                <CardMedia
                                    overlay={<CardTitle title="," subtitle="Zoom,,Zoom," />}
                                    >
                                    <img src="images/interior-of-library.jpg" />
                                </CardMedia>
                                <CardTitle title="" subtitle=" ,, " />

                                <CardText>

                                </CardText>
                                <CardActions>
                                    <RaisedButton
                                        label=""
                                        labelPosition="before"
                                        primary={true}
                                        icon={<ActionStoreIcon />}
                                        style={styles.button}
                                        />
                                    <RaisedButton
                                        label=""
                                        labelPosition="before"
                                        primary={true}
                                        icon={<ActionSearchIcon />}
                                        style={styles.button}

                                        onTouchTap={this.OpenAbout.bind(this)}
                                        />
                                </CardActions>
                            </Card>

                        </div>
                    </div>
                </Layout>
            </div>)
    }
}

// Home.propTypes = {

// };

export default withStyles(s)(Home);



